export class Api {
  constructor(data) {
    this._url = data.baseUrl;
    this._headers = data.headers;
    this._checkResponse = (res) => {
      if (res.ok) {
        return res.json();
      } else {
      return Promise.reject(`Ошибка: ${res.status}`);}
    };
  }

  //___PROFILE_________________________________________________________________________________

  getProfileInfo() {
    //console.log(this._url);
    console.log(this._headers);
    return fetch(`https://mesto.nomoreparties.co/v1/cohort-76/user/me`, {
      method: 'GET',
      headers: {
        authorization: '29f12257-2630-4f2f-9515-6ae5f2ed2223',
        'Content-Type': 'application/json'
      }
    })
      .then((res) => this._checkResponse(res));
  }

  editUserInfo(data) {
    return fetch(`${this._url}/user/me`, {
      method: "PATCH",
      headers: this._headers,
      body: JSON.stringify({
        name: data.name,
        about: data.description,
      }),
    }).then((res) => this._checkResponse(res));
  }

  //___AVATAR__________________________________________________________________________________

  editAvatar(data) {
    //console.log(data)
    return fetch(`${this._url}/users/me/avatar`, {
      method: "PATCH",
      headers: this._headers,
      body: JSON.stringify({
        avatar: data.avatar,
      }),
    }).then((res) => this._checkResponse(res));
  }

  //___CARD____________________________________________________________________________________

  getCards() {
    return fetch(`${this._url}/cards`, {
      headers: this._headers,
    }).then((res) => this._checkResponse(res));
  }

  postCard(data) {
    return fetch(`${this._url}/cards`, {
      method: "POST",
      headers: this._headers,
      body: JSON.stringify({
        name: data.name,
        link: data.link,
      }),
    }).then((res) => this._checkResponse(res));
  }

  putLike(cardId) {
    return fetch(`${this._url}/cards/${cardId}/likes`, {
      method: "PUT",
      headers: this._headers,
    }).then((res) => this._checkResponse(res));
  }

  deleteLike(cardId) {
    return fetch(`${this._url}/cards/${cardId}/likes`, {
      method: "DELETE",
      headers: this._headers,
    }).then((res) => this._checkResponse(res));
  }

  deleteCard(cardId) {
    return fetch(`${this._url}/cards/${cardId}`, {
      method: "DELETE",
      headers: this._headers,
    }).then((res) => this._checkResponse(res));
  }
}

